import React, { createContext, useReducer} from 'react'
import AppReducer from './AppReducer'
 
//Initial State
const initialState = {
    Btn1_status : false,
    Btn2_status : false,
    Btn3_status : false,
    flag_showProcessedVideo : false 
}

//Create Context object
export const GlobalContext = createContext(initialState);

export const GlobalProvider = ({children}) => {
    const [state,dispatch] = useReducer(AppReducer,initialState)

    const updateBtn_1 = (input) => {
        dispatch({
            type:'UPDATE_BTN1',
            payload:input
        })
    }
    const updateBtn_2 = (input) => {
        dispatch({
            type:'UPDATE_BTN2',
            payload:input
        })
    }
    const updateBtn_3 = (input) => {
        dispatch({
            type:'UPDATE_BTN3',
            payload:input
        })
    }
    const showProcessedVideo = (input) => {
        dispatch({
            type:'showProcessedVideo',
            payload:input
        })
    }
    const setPiData = (input) => {
        console.log(state)
        dispatch({
            type:'SETPIDATA',
            payload:input
        })
    }
    return(
       <GlobalContext.Provider value = {{
                state,
                updateBtn_1,
                updateBtn_2,
                updateBtn_3,
                setPiData 
        }}>

        {children}

        </GlobalContext.Provider>
    )
}
